import { NextResponse } from 'next/server';

export const dynamic = 'force-static';

// Mock Keycloak token response
export async function POST(request) {
  // Parse form data from request
  const formData = await request.formData();
  const grantType = formData.get('grant_type');
  const username = formData.get('username');
  const password = formData.get('password');
  const clientId = formData.get('client_id');
  
  // Simple validation
  if (!grantType || !clientId) {
    return NextResponse.json(
      { error: 'invalid_request', error_description: 'Missing required parameters' },
      { status: 400 }
    );
  }
  
  // For password grant type
  if (grantType === 'password' && (!username || !password)) {
    return NextResponse.json(
      { error: 'invalid_grant', error_description: 'Invalid credentials' },
      { status: 400 }
    );
  }
  
  // Mock successful response
  const mockToken = {
    access_token: 'eyJhbGciOiJSUzI1NiIsInR5cCIgOiAiSldUIiwia2lkIiA6ICJfT3B2Ym1RQTZGWl9NX2tzLWxpQlE4WFM0UkRFY0xfZHFMSGJlNkxJUjlNIn0..XYZ',
    expires_in: 300,
    refresh_expires_in: 1800,
    refresh_token: 'eyJhbGciOiJIUzI1NiIsInR5cCIgOiAiSldUIiwia2lkIiA6ICIyMzYyYWM3Yy1hZDVkLTQ4ZWUtODEzOC1iNDJmZmJkNzZhZjEifQ..ABC',
    token_type: 'Bearer',
    'not-before-policy': 0,
    session_state: 'a2ef2d34-3e24-4120-98d0-c2b6f3c5a361',
    scope: 'profile email'
  };
  
  return NextResponse.json(mockToken);
}

// Handle refresh token
export async function GET() {
  return NextResponse.json(
    { error: 'method_not_allowed', error_description: 'HTTP method not allowed' },
    { status: 405 }
  );
}
